@keyframes link-arrow-hover {
	0% {
		transform: translateX(0);
	}

	50% {
		transform: translateX(4px);
	}

	100% {
		transform: translateX(0);
	}
}

@keyframes pulse-1 {
	0% {
		transform: scale(1.0);
	}

	50% {
		transform: scale(1.3);
	}

	100% {
		transform: scale(1.0);
	}
}

@keyframes pulse-2 {
	0% {
		transform: scale(1.0);
	}

	50% {
		transform: scale(1.6);
	}

	100% {
		transform: scale(1.0);
	}
}
